import { useTranslation } from "./TranslationContext";
import Modal from "react-modal";
import "./modal.css";
import qr from "./images/qr.png"
import { useState } from "react";

Modal.setAppElement("#root");

const Download = () => {
  const { translate } = useTranslation();
  //   getPlatform
  const getPlatform = () => {
    const userDeviceArray = [
      { device: "Android", platform: /Android/ },
      { device: "iPhone", platform: /iPhone/ },
      { device: "iPad", platform: /iPad/ },
      { device: "Symbian", platform: /Symbian/ },
      { device: "Windows Phone", platform: /Windows Phone/ },
      { device: "Tablet OS", platform: /Tablet OS/ },
      { device: "Linux", platform: /Linux/ },
      { device: "Windows", platform: /Windows NT/ },
      { device: "Macintosh", platform: /Macintosh/ },
    ];

    const platform = navigator.userAgent;

    for (let i = 0; i < userDeviceArray.length; i++) {
      if (userDeviceArray[i].platform.test(platform)) {
        return userDeviceArray[i].device;
      }
    }
    return ` ! ${platform}`;
  };

  const goToAppStore = () => {
    let platform = getPlatform(); //    
    if (platform === "iPhone" || platform === "Macintosh") {
      window.location.href = "https://itunes.apple.com/app/id1452368807";
    } else if (platform === "Android") {
      window.location.href =
        "http://play.google.com/store/apps/details?id=islam.islamapp";
    }
  };

  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div className="button">
      <button className="page-title_nstall" onClick={goToAppStore}>
        {translate("download")}
      </button>
      <button className="qr_cod" onClick={openModal}>
         QR-
      </button>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Example Modal"
      >
        <h2></h2>
        <button className="modal-close-button" onClick={closeModal}></button>
        <img src={qr} className="qr"/>
      </Modal>
    </div>
  );
};

export default Download;
