import mockupEN from './images/mockupEN.svg';
import mockupRU from './images/mockupRU.svg';
import Download from "./Download";
import "./App.css";
import React, { useEffect, useCallback, useState } from 'react';
import { useTranslation } from "./TranslationContext";

//   
const images = {
  mockupEN,
  mockupRU,
};

const Home = () => {
  const { translate } = useTranslation();
  const mockup = translate("mockup");
  const [imagePath, setImagePath] = useState(null); //   imagePath

  useEffect(() => {
    if (mockup && mockup.image) { // ,  mockup  mockup.image 
      const path = images[mockup.image];
      if (path) {
        setImagePath(path);
      } else {
        console.error("Image not found for mockup:", mockup.image);
      }
    }
  }, [mockup]); //   mockup

  const handleResize = useCallback(() => {
    if (window.innerWidth > 768) {
      document.body.style.overflowY = 'hidden'; // overflowY   
    } else {
      document.body.style.overflowY = 'visible';
    }
  }, []);

  useEffect(() => {
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
      document.body.style.overflowY = 'visible'; //   
    };
  }, [handleResize]);

  if (!imagePath) {
    return <div>Loading...</div>; //   /-
  }

  return (
    <div className="title_inner">
      <div className="page-title">
        <h1 className="page-title_islamapp">IslamApp</h1>
        <p className="page-title_text">{translate("description")}</p>
        <Download />
      </div>

      <section className="islamApp">
        <div className="gradient-background"></div>
        <img className="islamApp_img" src={imagePath} alt={mockup.alt} />
        <img className="islamApp_img_1" src={imagePath} alt={mockup.alt} /> {/* ,      ? */}
      </section>
    </div>
  );
};

export default Home;